
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
form{
  border: 1px solid lightgrey;
  border-radius: 5px;
  padding: 10px;
  max-width: 690px;
}
.form-group--inline > * {
  display: inline-block;
  vertical-align: top;
}
.dropdown > * {
  width: 85%;
}
.dropdown-menu{
  height: 100px;
  overflow: scroll;
}
.dropdown-menu--visible{
  display: inherit;
  position: inherit;
  z-index: 0;
}
.menu-option{
  padding-left: 5px;
}
.selection--remove {
    border: 1px solid lightgrey;
    margin: 2px;
    float: left;
}
.selection--remove svg {
  color: #71777a;
}
.article-container{
  margin-top: 30px;
}
.article{
  border-bottom: 1px solid lightgrey;
  padding: 10px 0;
}
.article:first-child{
  padding-top: none;
}
.article:last-child{
  border-bottom: none;
  padding-bottom: none;
}
.paginator{
  padding-top: 10px;
  margin: 30px 0 60px 0;
}
footer > a {
  color: inherit;
}
footer > a:hover{
  color: inherit;
}
.ny-times-attribution{
  background: url(http://static01.nytimes.com/packages/images/developer/logos/poweredby_nytimes_150c.png);
  width: 150px;
  display: inline-block;
  height: 30px;
  background-repeat: no-repeat;
}
.brand-font{
  font-family: 'Cutive Mono', monospace;
}
i.disabled{
  cursor: inherit;
  color: lightgrey;
}
.brand{
    display: block;
    position: relative;
    top: 50%;
    right: 0;
    width: 15.8em;
    margin: 0 auto;
    font-size: 100%;
    text-align: center;
    white-space: nowrap;
    overflow: hidden;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
}
.icon-link:hover{
  text-decoration: none;
}
/* Animation */
.anim-typewriter{
  -webkit-animation: typewriter 4s steps(26) 1s 1 normal both;
          animation: typewriter 4s steps(26) 1s 1 normal both;
}
@-webkit-keyframes typewriter{
from{width: 0;
}
to{width: 15.8em;
}
}
@keyframes typewriter{
from{width: 0;
}
to{width: 15.8em;
}
}
@-webkit-keyframes blinkTextCursor{
from{border-right-color: rgba(0,0,0,.75);
}
to{border-right-color: transparent;
}
}
@keyframes blinkTextCursor{
from{border-right-color: rgba(0,0,0,.75);
}
to{border-right-color: transparent;
}
}
span.anim-loading{
  -webkit-animation-name: blink;
          animation-name: blink;
  -webkit-animation-duration: 1.4s;
          animation-duration: 1.4s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both;
  font-size: 2em;
}
span.anim-loading:nth-child(2){
  -webkit-animation-delay: .2s;
          animation-delay: .2s;
}
span.anim-loading:nth-child(3){
  -webkit-animation-delay: .4s;
          animation-delay: .4s;
}
@-webkit-keyframes blink{
0% { opacity: .2;
}
20% { opacity: 1;
}
100% { opacity: .2;
}
}
@keyframes blink{
0% { opacity: .2;
}
20% { opacity: 1;
}
100% { opacity: .2;
}
}
